import React, {Component} from 'react';
import { Button } from 'reactstrap';
// import ReactDOM from 'react-dom';
import Modal from 'react-modal';
// import { Redirect } from 'react-router'

class ModalExample extends Component {
	constructor() {
		super();
		
		this.state = {
			modalIsOpen: false,
			balance: '1000',
		};

		this.openModal = this.openModal.bind(this);
		this.afterOpenModal = this.afterOpenModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
	}

	openModal() {
		this.setState({ modalIsOpen: true });
	}

	afterOpenModal() {
		this.subtitle.style.color = 'black';
	}

	closeModal() {
		this.setState({ modalIsOpen: false });
	}

    openModalWithBalance(item) {
		this.setState({
			openDeleteModal: true,
			balance: this.state.item.balance,
		})
	}

	render() {
		
		const customStyles = {
			content : {
				top: '50%',
				left: '50%',
				right: 'auto',
				bottom: 'auto',
				marginRight: '-50%',
				transform: 'translate(-50%, -50%)',
				fontFamily: 'Sans-serif',
				background: '#ae4c05b6', /* fallback for old browsers */
				background: '-webkit-linear-gradient(to left, #f9d423, #9d7510b6)', /* Chrome 10-25, Safari 5.1-6 */
				background: 'linear-gradient(to right, rgb(179, 155, 33), #ae4c05b6)',
				borderRadius: '5px'
			}
		};

		this.state.modalIsOpen = this.props.open;
		
		this.state.balance = this.props.balance;

		return (
			<div>
				<Modal
					isOpen={this.state.modalIsOpen}
					onAfterOpen={this.afterOpenModal}
					onRequestClose={this.closeModal}
					style={customStyles}
					// contentLabel="Example Modal"
        		>
					<h2><strong>Thanks for participating!</strong></h2>
					<h2><strong>Your tokens will be sent.</strong></h2>
					<br />
					<h4 ref={subtitle => this.subtitle = subtitle}><strong>Your balance is: </strong></h4>
					<h3><strong>{this.state.balance} MT</strong></h3>
					<br />
					<a href="https://monarchtoken.io">
						<Button onClick={() => this.props.onClose()}><strong>Done</strong></Button>
					</a>
        		</Modal>
			</div>
		);
	}
}

export default ModalExample;


